import React, { Component } from 'react'
import {Route } from 'react-router-dom'
import MovieList from "./components/MovieList/movieList";
import Navbaar from './components/common/navbar'
import Home from './components/Home/home'
import MovieDetails from './components/movieDetails/movieDetails';
import TvShowDetails from './components/tvShowDetails/tvShowDetails'
import PopularTvShow from './components/TV-SHOWS/popular/popularTvShow'
import AiringTodayTvShow from './components/TV-SHOWS/airToday/airToday'
import OnTheAirTvShow from './components/TV-SHOWS/onTheAirTvShow/onTheAirTvShow'
import TopRatedTvShow from './components/TV-SHOWS/topRated/topRatedTvShow'


class App extends Component {
  render() {
      
        return (
          <div>
           <Navbaar />
           <Route path="/search" component={MovieList}/> 
           <Route path="/" exact component={Home} />
           <Route path="/movies" exact component={Home} /> 
           <Route path="/movie/:movietitle/:id" component={MovieDetails} />
           <Route path="/tv/popular" component={PopularTvShow} />
           <Route path="/tv/airing-today" component={AiringTodayTvShow}/>
           <Route path="/tv/on-tv" component={OnTheAirTvShow} />
           <Route path="/tv/top-rated" component={TopRatedTvShow}/>
           <Route path="/tv/:tvshowtitle/:id" component={TvShowDetails} />
           </div>
        );
      }
}
 
export default App;