import React, { useState, useEffect } from "react";
import {
  makeStyles,
  Grid,
  Card,
  Container,
  CardActionArea,
  CardMedia,
} from "@material-ui/core";
import { useHistory } from "react-router-dom";
import Carousel from "react-elastic-carousel";
import axios from "../../axios";

const userStyles = makeStyles((theme) => ({
  root: {
    minWidth: 140,
    flexGrow: 1
  },
}));

export default function TopRatedMovies() {
  const classes = userStyles();
  const [popular, setPopular] = useState([]);

  useEffect(() => {
    axios.get("/topratedmovies").then((response) => {
      setPopular(response.data);
      console.log(popular);
    });
  }, []);

  const history = useHistory();

  return (
    <div className="carousel-wrapper">
      <Container
        maxWidth="xl"
        style={{ height: "45vh", paddingTop: 8 }}
      >
        <h2 className="popular" style={{ paddingBottom: '1rem' }}>Top rated Movies</h2>
        <Grid
          container
          spacing={3}
          direction="row"
          justify="flex-start"
          alignItems="flex-start"
        >
          <Carousel itemsToShow={6} itemsToScroll={1} pagination={false} showArrows={true}>
            {popular.map((popular, index) => (
              <Grid item xs={12} sm={6} key={index}>
               <Card
                  className={classes.root}
                  onClick={() => {
                    history.push(`/movie/${popular[0]}/${popular[3]}`);
                  }}
                >
                  <CardActionArea>
                    <CardMedia
                      component="img"
                      objectFit="cover"
                      image={popular[1]}
                      title={popular[0]}
                    />
                  </CardActionArea>
                </Card>
              </Grid>
            ))}
          </Carousel>
        </Grid>
      </Container>
    </div>
  );
}
