import React from "react";
import { fade, makeStyles } from "@material-ui/core";
import SearchIcon from "@material-ui/icons/Search";
import SearchBar from "../SearchBar/searchBar";
import { Navbar, Nav, NavDropdown } from "react-bootstrap";
import "./navbar.css";

const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 5,
  },
  home: {
    flexGrow: 1,
    marginLeft: theme.spacing(3),
    paddingBottom: 4,
  },
  title: {
    display: "none",
    [theme.breakpoints.up("sm")]: {
      display: "block",
    },
  },
  search: {
    position: "relative",
    borderRadius: theme.shape.borderRadius,
    backgroundColor: fade(theme.palette.common.white, 0.15),
    "&:hover": {
      backgroundColor: fade(theme.palette.common.white, 0.25),
    },
    marginLeft: 0,
    width: "100%",
    [theme.breakpoints.up("sm")]: {
      marginLeft: theme.spacing(1),
      width: "auto",
    },
  },
  searchIcon: {
    padding: theme.spacing(0, 2),
    height: "100%",
    position: "absolute",
    pointerEvents: "none",
    display: "flex",
    alignItems: "center",
    justifyContent: "center",
  },
}));

export default function Navbaar() {
  const classes = useStyles();

  return (
    <Navbar
      collapseOnSelect
      style={{ backgroundColor: "#212121", height: "4rem" }}
      variant="dark"
      expand="lg"
      sticky="top"
    >
      <Navbar.Brand style={{ fontSize: "1.6rem", marginLeft: "5rem" }}>
        MOVIESPOT
      </Navbar.Brand>
      <Navbar.Toggle aria-controls="responsive-navbar-nav" />
      <Navbar.Collapse id="responsive-navbar-nav">
        <Nav className="mr-auto">
          <Nav.Link href="/movies" style={{ fontSize: "1.4rem" }}>
            Movies
          </Nav.Link>
          <NavDropdown
            title="TV Shows"
            id="collasible-nav-dropdown"
            style={{ fontSize: "1.3rem", paddingTop: "0.6%" }}
          >
            <NavDropdown.Item href="/tv/popular">Popular</NavDropdown.Item>
            <NavDropdown.Item href="/tv/airing-today">
              Airing Today
            </NavDropdown.Item>
            <NavDropdown.Item href="/tv/on-tv">On Tv</NavDropdown.Item>
            <NavDropdown.Item href="/tv/top-rated">Top Rated</NavDropdown.Item>
          </NavDropdown>
        </Nav>
        <div className={classes.search}>
          <div className={classes.searchIcon}>
            <SearchIcon />
          </div>
          <SearchBar />
        </div>
      </Navbar.Collapse>
    </Navbar>
  );
}
