import React, { useState, useEffect } from "react";
import ReactPlayer from "react-player";
import { useParams } from "react-router-dom";
import axios from "axios";
import RecommendedTvShow from "../TV-SHOWS/recommendations/recommendedTvshow";


import "./tvShowDetails.css";

export default function TvShowDetails() {
  const { id } = useParams();
  const [TvShowVideo, setTvShowVideo] = useState([]);
  const [TvShowDetails, setTvShowDetails] = useState([]);
  const [TvShowGenres, setTvShowGenres] = useState([]);

  useEffect(() => {
    axios
      .get(
        `https://api.themoviedb.org/3/tv/${id}/videos?api_key=eeeec0d10ea9ef7bd9542bc1c8caac5a&language=en-US`
      )
      .then((res) => {
        if (
          typeof res.data.results !== "undefined" &&
          res.data.results.length > 0
        ) {
          setTvShowVideo(res.data.results[0].key);
        } else {
          setTvShowVideo();
        }
      });
    axios
      .get(
        `https://api.themoviedb.org/3/tv/${id}?api_key=${process.env.REACT_APP_API_KEY}&language=en-US`
      )
      .then((res) => {
        setTvShowDetails(res.data);
        setTvShowGenres(res.data.genres);
      });
  }, []);

  let time = TvShowDetails.episode_run_time;
  var Hours = Math.floor(time / 60);
  var minutes = time % 60;

  return (
    <div>
      <div style={{ backgroundColor: "#282828", height: "26rem" }}>
        <div className="row gx-5">
          <div className="col">
            <div className="player-wrapper">
              <ReactPlayer
                className="react-player"
                url={`https://www.youtube.com/watch?v=${TvShowVideo}`}
                controls={true}
                width="100%"
                height="112%"
              />
            </div>
          </div>
          <div className="col">
            <div className="title"> {TvShowDetails.original_name} </div>
            <ul className="list-inline">
              <li className="list-inline-item">
                { time > 0 && (
                  <h4>
                    {" "}
                    {Hours} hrs {minutes} min,{" "}
                  </h4>
                )}
              </li>
              <li className="list-inline-item">
                {TvShowGenres.map((data) => {
                  return (
                    <li key={data.id} className="list-inline-item">
                      <h4>{data.name},</h4>
                    </li>
                  );
                })}
              </li>
              <li className="list-inline-item">
                {TvShowDetails.original_language === "en" && <h4> English </h4>}
                {TvShowDetails.original_language === "ja" && <h4> Japanese </h4>}
                {TvShowDetails.original_language === "ko" && <h4> Korean </h4>}
                {TvShowDetails.original_language === "hi" && <h4> Hindi </h4>}
              </li>
              <li></li>
            </ul>
            <div className="tagline" >
              {TvShowDetails.tagline}
            </div>
            <h4>Overview:</h4>
            <div className="overview">
              <p>{TvShowDetails.overview}</p>
            </div>
          </div>
        </div>
      </div>
      <div style={{ backgroundColor: "black" }}>
        <RecommendedTvShow />
      </div>
    </div>
  );
}
