import React, { useState, useEffect } from "react";
import {
  makeStyles,
  Grid,
  Card,
  Container,
  CardActionArea,
  CardMedia,
} from "@material-ui/core";
import { useHistory } from "react-router-dom";
import axios from "../../../axios";

const userStyles = makeStyles((theme) => ({
  root: {
    minWidth: 180,
  }
}));

export default function AiringTodayTvShow() {
  const classes = userStyles();
 
  const [popular, setPopular] = useState([]);

  useEffect(() => {
    axios.get("/tv/airingtodaytvshow").then((response) => {
      setPopular(response.data);
    });
  }, []);

  const history = useHistory();

  return (
    <div>
      <Container
        maxWidth="xl"
        style={{ backgroundColor: "black", paddingTop: 8 }}
      >
        <h2 style={{ paddingBottom: '2rem', paddingTop: '2rem', color: 'white' }}>Airing Today</h2>
        <Grid
          container
          className={classes.root}
          spacing={3}
          direction="row"
          justify="flex-start"
          alignItems="center"
        >
          {popular.map((popular, index) => (
            <Grid item xs={12} sm={2} key={index}>
              <Card
                className={classes.root}
                onClick={() => {
                  history.push(`/tv/${popular[0]}/${popular[2]}`);
                }}
              >
                <CardActionArea>
                  <CardMedia
                    component="img"
                    objectFit="cover"
                    image={popular[1]}
                    title={popular[0]}
                    id={popular[2]}
                  />
                </CardActionArea>
              </Card>
            </Grid>
          ))}
        </Grid>
      </Container>
    </div>
  );
}
