import React, { useState, useEffect } from "react";
import ReactPlayer from "react-player";
import { useParams } from "react-router-dom";
import axios from "axios";
import MovieRecommendations from '../RecommendedMovies/recommendedMovies'
import "./movieDetails.css";

export default function MovieDetails() {
  const { id } = useParams();
  const [movieVideo, setMovieVideo] = useState([]);
  const [movieDetails, setMovieDetails] = useState([]);
  const [movieGenres, setMovieGenres] = useState([]);

  useEffect(() => {
    axios
      .get(
        `https://api.themoviedb.org/3/movie/${id}/videos?api_key=${process.env.REACT_APP_API_KEY}&language=en-US`
      )
      .then((res) => {
        if (
          typeof res.data.results !== "undefined" &&
          res.data.results.length > 0
        ) {
          setMovieVideo(res.data.results[0].key);
        } else {
          setMovieVideo();
        }
      });
    axios
      .get(
        `https://api.themoviedb.org/3/movie/${id}?api_key=${process.env.REACT_APP_API_KEY}&language=en-US`
      )
      .then((res) => {
        setMovieDetails(res.data);
        setMovieGenres(res.data.genres);
      });
  }, []);

  let time = movieDetails.runtime;
  var Hours = Math.floor(time / 60);
  var minutes = time % 60;

  return (
    <div>
      <div style={{ backgroundColor: "#282828", height: "26rem" }}>
        <div className="row gx-5">
          <div className="col">
            <div className="player-wrapper">
              <ReactPlayer
                className="react-player"
                url={`https://www.youtube.com/watch?v=${movieVideo}`}
                controls={true}
                width="100%"
                height="112%"
              />
            </div>
          </div>
          <div className="col">
            <div className="title"> {movieDetails.title} </div>
            <ul className="list-inline">
              <li className="list-inline-item">
                {time > 0 && (
                  <h4>
                    {" "}
                    {Hours} hrs {minutes} min,{" "}
                  </h4>
                )}
              </li>
              <li className="list-inline-item">
                {movieGenres.map((data) => {
                  return (
                    <li key={data.id} className="list-inline-item">
                      <h4>{data.name},</h4>
                    </li>
                  );
                })}
              </li>
              <li className="list-inline-item">
                {movieDetails.original_language === "en" && <h4> English </h4>}
                {movieDetails.original_language === "ja" && <h4> Japanese </h4>}
                {movieDetails.original_language === "ko" && <h4> Korean </h4>}
                {movieDetails.original_language === "hi" && <h4> Hindi </h4>}
              </li>
              <li></li>
            </ul>
            <div className="tagline" >
              {movieDetails.tagline}
            </div>

            <h4>Overview:</h4>
            <div className="overview">
              <p>{movieDetails.overview}</p>
            </div>
          </div>
        </div>
      </div>
      <div style={{ backgroundColor: "black" }}>
        <MovieRecommendations />
      </div>
    </div>
  );
}
