import React, { useState, useEffect } from "react";
import "./movieList.css";
import {
  makeStyles,
  Grid,
  Card,
  CardActionArea,
  CardMedia,
} from "@material-ui/core";
import { useHistory, useLocation } from "react-router-dom";
import axios from "axios";
import qs from "qs";

const userStyles = makeStyles(() => ({
  root: {
    minWidth: 180,
  },
}));

export default function MovieList() {
  const classes = userStyles();
  let location = useLocation();
  let name = qs.parse(location.search, { ignoreQueryPrefix: true }).query;
  const [data, setData] = useState([]);

  useEffect(() => {
    axios
      .get(
        `https://api.themoviedb.org/3/search/movie?api_key=${process.env.REACT_APP_API_KEY}&language=en-US&query=${name}&page=1&include_adult=false`
      )
      .then((response) => {
        if (response.data != null) {
          setData(response.data.results);
        } else {
          setData(null);
        }
      });
  }, []);

  const history = useHistory();
  return (
    <div className="spaceing">
      <Grid
        container
        spacing={3}
        direction="row"
        justify="flex-start"
        alignItems="flex-start"
      >
        {data.map((data, index) =>
          data.poster_path !== null ? (
            <Grid item xs={12} sm={2} md={20} key={index}>
              <Card
                className={classes.root}
                onClick={() => {
                  history.push(`/movie/${data.title}/${data.id}`);
                }}
              >
                <CardActionArea>
                  <CardMedia
                    component="img"
                    objectFit="cover"
                    image={"https://image.tmdb.org/t/p/w500" + data.poster_path}
                    title={data.title}
                  />
                </CardActionArea>
              </Card>
            </Grid>
          ) : null
        )}
      </Grid>
    </div>
  );
}
